<template>
  <lay-header :showBack="true" :tBarColor="'#FFFFFF'" :txtTitle="'건의사항'">
    <v-layout column class="pt-4 px-5 pb-6 lay-body">
      <span class="txt-desc"
        >다이브로이드에서는 회원님이 보내주시는 의견을 항상 경청하고 있습니다.
        모든 의견에 개별적으로 회신드릴 수는 없지만, 알려주신 내용은
        다이브로이드 서비스 개선을 위해 담당부서에 꼭 전달하겠습니다.
      </span>
      <span class="txt-sub mt-5">제목</span>
      <c-input
        :hint="inputList.title.hint"
        :value.sync="inputList.title.value"
      />
      <span class="txt-sub mt-10">건의 내용</span>
      <v-textarea
        v-model="inputList.cont.value"
        rows="7"
        row-height="24"
        hide-details
        outlined
        no-resize
        :placeholder="inputList.cont.hint"
        maxlength="5000"
        class="mt-4 flex-grow-0"
        color="rgba(21, 212, 224, 0.3)"
      />
      <span class="align-self-end mt-2 txt-cnt"
        ><span>{{
          inputList.cont.value ? inputList.cont.value.length : 0
        }}</span
        >/5000</span
      >
      <v-layout class="flex-grow-0">
        <img
          v-ripple
          src="@/assets/images/ic-attach-24.svg"
          class="ic-attach"
          @click.prevent.stop="onClickActionFile"
        />
        <v-layout row wrap class="lay-attach ml-2">
          <v-img
            v-for="(item, index) in uploadFiles"
            :key="index"
            src="@/assets/images/img-main-bg.png"
            class="img-attach"
            aspect-ratio="1"
            :max-width="pxToRem(64)"
          >
            <img src="@/assets/images/ic-circle-x.svg" class="ic-x" />
            <span class="txt-duration">00:52</span>
          </v-img>
        </v-layout>
      </v-layout>
      <v-file-input
        v-model="files"
        ref="uploader"
        v-show="false"
        :multiple="false"
        @change="inputFileChange"
      />
      <span class="txt-file-sub mt-3 mb-2">
        파일에 개인정보가 포함되지 않도록 주의해 주세요.<br />
        사진은 5개까지, 동영상은 1분 내 길이로 2개까지 등록 가능합니다.
      </span>
    </v-layout>
    <c-footer height="86" class="lay-footer px-5 py-4" elevation="0">
      <c-btn
        :thema="'default-bg'"
        :onClick="
          () =>
            pushRouter({
              name: 'qnaReg',
            })
        "
        >등록하기</c-btn
      >
    </c-footer>
    <v-bottom-sheet scrollable inset v-model="bPop.isShow">
      <p-select
        v-model="types"
        :selectIdx="typeIndex"
        :onClose="
          (idx) => {
            typeIndex = idx;
            inputList.type.value = types[idx].title;
            onClosePopupAction(bPop);
          }
        "
      />
    </v-bottom-sheet>
  </lay-header>
</template>

<script>
import cListItem from "@/components/cListItem.vue";
import PSelect from "../../popup/pSelect.vue";
import CFooter from "@/components/cFooter.vue";
export default {
  components: { cListItem, PSelect, CFooter },
  data() {
    return {
      files: [],
      uploadFiles: [],
      typeIndex: null,
      emailCheck: false,
      inputList: {
        type: {
          value: null,
          hint: "유형을 선택하세요",
        },
        title: {
          value: null,
          hint: "제목을 입력하세요",
        },
        cont: {
          value: null,
          hint: "",
        },
        name: {
          value: null,
          hint: "이름",
        },
        loc: {
          value: null,
          hint: "+82",
        },
        tel: {
          value: null,
          hint: "전화번호",
        },
      },
      bPop: {
        isCreate: false,
        isShow: false,
      },
      types: [
        {
          title: "다이브로이드 기기",
          ic: require("@/assets/images/ic-equipment-20.svg"),
          seq: "001",
        },
        {
          title: "액세서리",
          ic: require("@/assets/images/ic-accessary-20.svg"),
          seq: "002",
        },
        {
          title: "고장/수리/오작동",
          ic: require("@/assets/images/ic-brokedown-20.svg"),
          seq: "003",
        },
        {
          title: "앱 사용",
          ic: require("@/assets/images/ic-app-20.svg"),
          seq: "004",
        },
        {
          title: "계정 및 회원 정보",
          ic: require("@/assets/images/ic-my-20.svg"),
          seq: "005",
        },
        {
          title: "구매/반품/환불",
          ic: require("@/assets/images/ic-box-20.svg"),
          seq: "006",
        },
        {
          title: "배송",
          ic: require("@/assets/images/ic-delivery-20.svg"),
          seq: "007",
        },
        {
          title: "기타",
          ic: require("@/assets/images/ic-etc-20.svg"),
          seq: "008",
        },
      ],
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  destroyed() {},
  methods: {
    onClickActionType() {},
    onClickActionFile() {
      if (this.uploadFiles.length < 5) {
        this.$refs.uploader.$refs.input.click();
      } else {
        this.showMsgPopup("첨부파일은 최대 5개까지 등록가능합니다.");
      }
    },
    inputFileChange(val) {
      val.forEach((file) => {
        if (this.uploadFiles.length > 5) {
          this.showMsgPopup("첨부파일은 최대 5개까지 등록가능합니다.");
          return;
        }
        // if (!this.uploadFiles.some((f) => f.name == file.name)) {
        //     this.uploadFiles.push(file);
        // }

        if (file.size > 10 * 1024 * 1024) {
          this.showMsgPopup("첨부파일은 최대 10MB의 용량제한이 있습니다.");
          return;
        }

        if (this.accept != "*/*") {
          var extName = file.name
            .substring(file.name.lastIndexOf(".") + 1)
            .toLowerCase();
          var str = this.$refs.uploader.$refs.input.accept
            .replace(/\./g, "")
            .replace(/(\s*)/g, "")
            .toLowerCase()
            .split(",");
          if (str.indexOf(extName) < 0) {
            this.showMsgPopup(
              "업로드 가능한 파일 유형은 " +
                this.$refs.uploader.$refs.input.accept
                  .replace(/\./g, "")
                  .toUpperCase() +
                " 입니다."
            );
            return;
          }
        }

        if (!this.uploadFiles.some((f) => f.oriFileName == file.name)) {
          let tFile = {
            oriFileName: file.name,
            fileSize: file.size,
            type: file.type.toLowerCase().startsWith("image")
              ? 1
              : file.type.toLowerCase().startsWith("video")
              ? 2
              : 3,
            file: file,
          };
          this.uploadFiles.push(tFile);
        }
      });
      // this.uploadFiles = this.uploadFiles.concat(val)
    },
    removeFile(val) {
      this.uploadFiles.splice(val, 1);
    },
  },
};
</script>

